@import url(https://rsms.me/inter/inter.css);
@import url(https://fonts.googleapis.com/css?family=Cinzel|Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Quicksand:400,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nixie+One&display=swap);
/* @import url("https://fonts.googleapis.com/css2?family=Poiret+One&display=swap"); */
/* @import url("https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap"); */

@media (prefers-color-scheme: dark) {
  :root {
    /* --bg-color: #353b48 !important; */
    --bg-color: #121212 !important;
    --avatar-shadow: rgba(0, 0, 0, 0.6) !important;
    --button-bg-color: var(--link-color-hover);
    --link-color: #a29bfe;
    --link-color-hover: #6c5ce7;
    --text-color: #ffffff;
    --button-color-hover: var(--link-color-hover);
    --button-color: #ffffff;
    --sm-link-color: var(--link-color-hover);
    --selection-color-bg: #a29bfe;
    --selection-color: #ffffff;
    --heading-color: #f5f6fa;
  }

  body,
  #root {
    background: linear-gradient(45deg, #333333 0%, #111111 100%) !important;
    /* width: 100%; */
  }

  #main #left img {
    /* border: 2px dotted var(--link-color); */
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  }
}

@media (prefers-color-scheme: light) {
  :root {
    --bg-color: #f5f6fa;
    --avatar-shadow: rgba(0, 0, 0, 0.1);
    --text-color: #000000;
    --button-bg-color: #2f3640;
    --link-color: #0097e6;
    --link-color-hover: #00a8ff;
    --text-color: #000000;
    --button-color: #f5f6fa;
    --sm-link-color: #0097e6;
    --selection-color: #ffffff;
    --selection-color-bg: rgba(0, 151, 230, 1);
    --heading-color: #353b48;
  }
}

html {
  font-family: "Inter", sans-serif;

  overflow: hidden;
}
@supports (font-variation-settings: normal) {
  html {
    font-family: "Inter var", sans-serif;
  }
}

::selection {
  background-color: var(--selection-color-bg);
  color: var(--selection-color);
}

* {
  margin: 0;
  padding: 0;
}

body,
#root {
  height: 100vh;
  scroll-behavior: smooth;
  background-color: var(--bg-color);
  font-family: "Inter", sans-serif;
  display: grid;
  place-items: center;
  color: var(--text-color);
  width: 100%;
}
@media screen and (max-width: 1200px) {
  /* Homepage CSS */
  #main {
    flex-direction: column;
    text-align: center;
    width: 70%;
  }
  #main img {
    height: 10rem;
    border-radius: 4% !important;
  }
  #main h1 {
    font-size: 32px;
    margin: 16px auto 8px auto !important;
  }
  #main .divider {
    display: none !important;
  }

  /* Not Found Page CSS */
  #not-found .container-h .giant {
    font-size: 73.5px !important;
  }

  #not-found .container-h .small {
    font-size: 17.5px !important;
  }

  /* Global CSS */
  #copyright {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  #main {
    width: 40%;
  }
}

#main {
  display: flex;
  padding: 20px 0;
}

#main #left {
  display: grid;
  align-items: center;
  justify-content: center;
}

#main #left img {
  max-height: 10rem;
  border-radius: 5rem;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

#main #left img:hover {
  box-shadow: 0 8px 32px var(--avatar-shadow);
  /* max-height: 11rem; */
  /* border-radius: 5.5rem; */
}

#main h1 {
  font-family: "Cinzel", "Montserrat", "Inter", sans-serif;
  display: block;
  font-weight: unset;
  cursor: default;
  text-align: center;
  margin: 0 auto;
}
#main #right {
  display: flex;
  flex-direction: column;
}
#main #right p {
  margin: 16px 0 !important;
  cursor: default;
  font-size: 14px;
  text-align: center;
}
#main #mail {
  display: flex;
  text-decoration: none;
  align-items: center;
  background-color: var(--button-bg-color);
  height: 24px;
  border-radius: 2px;
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
  color: var(--button-color);
  transition: all 0.5s ease-in-out;
}
#main #mail div {
  margin: 0 auto;
}
#main #mail span {
  margin-left: 4px;
}
#main #mail:hover {
  background-color: white;
  box-shadow: 0 8px 32px var(--avatar-shadow);
}
#main #mail:hover {
  text-decoration: none;
  color: var(--button-color-hover);
}
.link {
  text-decoration: underline;
  color: var(--link-color);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.link:hover {
  color: var(--link-color-hover);
}
#social-icons {
  display: flex;
  place-content: space-evenly;
}
#social-icons a {
  color: var(--text-color);
  text-decoration: none;
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
  margin-top: 8px;
}
#social-icons a:hover {
  text-decoration: none;
  color: var(--sm-link-color);
}
.noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently supported by Chrome, Opera and Firefox */
}
#copyright {
  font-family: "Montserrat", "Inter", sans-serif;
  color: rgba(0, 0, 0, 0);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  position: absolute;
  bottom: 5vh;
  width: 100%;
  text-align: center;
  font-size: 12px;
}
#copyright:hover {
  color: var(--text-color);
}
.divider {
  background-color: #dcdde1;
  display: block;
  margin: 0 32px !important;
  height: inherit;
  width: 4px;
}
a {
  text-decoration: underline;
  color: var(--link-color);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
a:hover {
  color: var(--link-color-hover);
}
#not-found {
  text-align: center;
}
#not-found p:first-of-type {
  margin-top: 32px;
}
#not-found p {
  cursor: default;
}
#not-found .container-h {
  margin: 0 auto;
  display: flex;
  place-content: center;
  align-items: baseline;
  align-content: flex-end;
  color: var(--heading-color);
  /* background-color: slateblue; */
}
#not-found .container-h .divider {
  margin: 0 16px !important;
  visibility: hidden;
}
#not-found .container-h .giant {
  font-family: "Inter", "Montserrat", sans-serif !important;
  font-size: 147px;
  font-weight: 900 !important;
}
#not-found .container-h .small {
  text-align: left;
  /* background-color: red; */
  height: 100%;
  /* margin-top: auto; */
  /* margin-bottom: 5%; */
  font-weight: 900;
  font-size: 35px;
  font-family: "Inter", "Montserrat", sans-serif !important;
}
#not-found .content {
  width: 85%;
  margin: 0 auto;
}

